.Persik {
  display: block;
  width: 30%;
  max-width: 240px;
  margin: 20px auto;
}

.vivod {
	margin: 5px auto;
}

body {
	color: black;
}

.btn-ogan-vlad-lox {
	color: black;
	font-family: Arial, 'Times New Roman', Times;
	padding-left: 5px;
	padding-bottom: 2px;
}

.link {
	text-decoration: none;
}

.stavka-vlad-lox {
    padding: 9px 40px;
    border-radius: 35px;
    background: #fff;
    font-weight: 600;
    font-size: 25px;
    box-shadow: 0 0 6px rgba(0,0,0,.4);
}

.game {
    padding: 9px 40px;
    border-radius: 35px;
    background: #fff;
    font-weight: 600;
    font-size: 25px;
    box-shadow: 0 0 6px rgba(0,0,0,.4);
}

.game__coin {
    display: inline-block;
    vertical-align: -6px;
}